                    'nome': 'Desmatamento',
                    'slug': 'esquerda',
                    'texto': '<div class="text has-image ">    <p dir="ltr">Em debate promovido por        <strong>UOL        </strong>, Folha, Band e TV Cultura neste domingo (16), os presidenciveis        <a href="https://noticias.uol.com.br/politica/governo-bolsonaro/">Jair Bolsonaro        </a> (PL) e        <a href="https://noticias.uol.com.br/politica/lula/">Luiz Incio Lula da Silva        </a> (PT) discutiram sobre as taxas de desmatamento na Amaznia. Bolsonaro afirmou que, durante o primeiro        mandato do petista o desmatamento foi maior que o dobro do registrado no seu governo. "Voc desmatou 2,5 vezes        mais do que no meu governo", disse ao concorrente. J Lula, afirmou que "o menor desmatamento da Amaznia foi no        nosso governo".    </p>    <p dir="ltr">O        <strong>menor ndice da srie histrica foi registrado no ano de 2012        </strong>, durante o governo de Dilma Rousseff (PT). Embora as taxas dos trs primeiros anos da gesto de Lula        (que governou por oito anos) tenham sido maiores que as dos trs primeiros anos de Bolsonaro, o        <strong>petista reduziu        </strong>        <strong> o ndice a partir de 2005, enquanto sob Bolsonaro o desmatamento registrado na regio s aumenta a cada            ano        </strong>.    </p>    <p style="margin-bottom: 0;">    </p>    <p dir="ltr">        <strong>Comparaes.        </strong>Como mostra        <a            href="https://noticias.uol.com.br/confere/ultimas-noticias/2022/09/02/bolsonaro-distorce-dados-do-desmatamento-em-comparacao-com-governo-lula.htm">matria            do UOL Confere        </a>, o        <strong>desmatamento acumulado na Amaznia nos primeiros trs anos do governo Lula (2003 a 2005) foi maior que o            dobro do perodo entre 2019 a 2021        </strong>, primeiros trs anos do governo Bolsonaro. Mas a        <strong>taxa herdada pelo petista ao assumir o governo foi quase o triplo da que Bolsonaro encontrou ao assumir            a presidncia        </strong>. Enquanto, sob o governo Lula, o ndice passou a cair a partir de 2005, em todos os anos do governo        Bolsonaro a taxa cresce. O        <strong>UOL Confere        </strong> considerou os trs primeiros anos de cada gesto, e no os quatro, como disse Bolsonaro, porque a        srie histrica de 2022 no est completa.    </p>    <p dir="ltr">        <strong>Lula x Bolsonaro.        </strong>Segundo dados do        <a href="http://terrabrasilis.dpi.inpe.br/app/dashboard/deforestation/biomes/legal_amazon/rates"> PRODES        </a>, o sistema de monitoramento do Inpe (Instituto Nacional de Pesquisas Espaciais), a rea total desmatada na        Amaznia Legal de 2003 a 2005 primeiros trs anos de governo Lula foi de 72.128 km. J nos primeiros trs        anos sob Bolsonaro, de 2019 a 2021, a perda acumulada foi de 34.018 km menos da metade.    </p>    <p dir="ltr">        <strong>Heranas diferentes.        </strong>Porm em 2002, quando Lula foi eleito, o desmatamento na Amaznia foi de 21.650 km. J em 2018, quando        Bolsonaro se elegeu, a taxa foi de 7.536 km, quase trs vezes menor.    </p>    <p dir="ltr">A partir de 2005, porm, o desmatamento no bioma comeou a ser contido e registrou quedas sucessivas        pelos anos seguintes. Quando Lula entregou o governo, em 2010, a taxa havia cado para um tero da registrada em        2002.    </p>    <p dir="ltr">        <strong>Enquanto o governo Lula passou a reduzir os ndices a partir do terceiro ano de governo, a gesto            bolsonarista vem registrando altas sucessivas        </strong> desde o incio, acelerando uma curva de crescimento que vinha desde 2017. O aumento acumulado do        desmatamento sob Bolsonaro  de 73%.    </p>    <p dir="ltr">        <strong>Menor ndice.        </strong> A        <a href="http://terrabrasilis.dpi.inpe.br/app/dashboard/deforestation/biomes/legal_amazon/rates">menor taxa de            desmatamento anual na Amaznia foi registrada em 2012        </a>, no governo da petista Dilma Rousseff, de acordo com a srie histrica do PRODES, do Inpe. Naquele ano a        taxa de desmatamento da Amaznia Legal foi de 4.571        <span>km        </span>.    </p></div>',
                    'referencia': 'https://noticias.uol.com.br/confere/ultimas-noticias/2022/10/16/nem-bolsonaro-nem-lula-menor-desmatamento-na-amazonia-e-de-governo-dilma.htm',
