                    'nome': 'Pandemia  s uma gripezinha',
                    'slug': 'pandemia-e-so-uma-gripezinha',
                    'texto': '<iframe width="560" height="315" src="https://www.youtube.com/embed/rcxB7DsEAFQ" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe> <br /> <iframe width="560" height="315" src="https://www.youtube.com/embed/o-Qoib3S1rA" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>',
